window.jQuery = function (selector) {
  const elements = document.querySelectorAll(selector)
  //api可以操作elements
  //const api = {
  return {
    //闭包：函数访问外部变量
    addClass(className) {
      for (let i = 0; i < elements.length; i++) {
        elements[i].classList.add(className)
      }
      // obj.fn(p1) === obj.fn.call(obj,p1) 函数里的this就是obj
      //return api
      return this
    }
  }
  //return api
}